@import url('https://fonts.googleapis.com/css?family=IBM+Plex+Mono:100,100i,200,200i,300,300i,400,400i,500,500i,600,600i,700,700i&display=swap');
@import url('https://fonts.googleapis.com/css?family=IBM+Plex+Sans:100,100i,200,200i,300,300i,400,400i,500,500i,600,600i,700,700i&display=swap');



 ///////////////////////  RESET 
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}




body{
    background-color: #292929 !important;
    padding: 0px;
    margin: 0px;
}


.App{
    padding: 96px 0px 56px 0px;
    color: #ffffff;
    max-width: 1440px;
    margin: 0 auto;
}



::-webkit-scrollbar {
    width: 5px;
  }
::-webkit-scrollbar-track {
    -webkit-box-shadow: inset 0 0 0px transparent;
  }
::-webkit-scrollbar-thumb {
  background-color: #ffffff;
} 


/*For mozilla */
:root{
    scrollbar-color: rgb(255, 255, 255) transparent !important;
    scrollbar-width: thin !important;
  }


  /* Landscape phones and down */
@media (min-width: 320px) and (max-width: 480px){
  .App{
    padding: 10px !important;
  }

}

/* Landscape phone to portrait tablet */
