.left__content{
    display: grid;
    height: auto;
    grid-gap: 38px 0px;
}

h1{
    font-size: 80px;
    font-weight: bold;
    line-height: 104px;
    text-align: left;
    vertical-align: top;
    margin-top: 0px;
}

#root > div > div > div.left__content > p{
    color: #B3B3B3;
    font-size: 16px;
    font-style: regular;
    line-height: 24px;
    text-align: left;
    vertical-align: top;
    margin-block-start: 0px;
    margin-block-end: 0px;
}

.social{
    display: flex;
    align-items: center;
}

#root > div > div > div.left__content > section{
    display: grid;
    grid-gap: 0px 0px;
    margin-top: 176px;
}

#root > div > div > div.left__content > section:nth-child(5){
    margin-top: 610px;
}

#proj_contact > a:nth-child(n){
    color: #B2B2B2;
    display: block !important;
    font-weight: bold;
    font-size: 16px;
    line-height: 24px;
    word-spacing: 52px;
    width: fit-content;
    text-transform: uppercase;
    text-decoration: none;
}


#root > div > div > div.left__content > section > section.social > img{
    width: 48px;
    height: 48px;
    background: grey;
    border-radius: 25px;
}

#root > div > div > div.left__content > section  > section.social > a:nth-child(n){
    margin-left: 32px;
    color: #FFFFFF;
    text-transform: uppercase;
    text-decoration: none;
}


/* burger menu */

#root > div > div > div.left__content > nav{
    display: block;
    right: 26px;
    position: absolute;
    top: 26px;
}


#myLinks {
    display: none;
    float: left;
    right: 45px;
    position: relative;
    top: -8px;
}

#myLinks > a:nth-child(n){
    color: #B2B2B2;
    font-weight: bold;
    font-size: 14px;
    line-height: 24px;
    word-spacing: 52px;
    text-decoration: none;
    text-transform: uppercase;
    padding-right: 12px;
}

#root > div > div > div.left__content > nav > div{
    cursor: pointer;
    float: left;
    width: 20px;
    height: 4px;
}


.top__bar{
    display: block;
    border: 1px solid #B2B2B2;
    transform: rotate( 0deg);
    width: 15px;
    top: -3px;
    position: relative;
}


.bottom__bar{
    display: block;
    border: 1px solid #B2B2B2;
    transform: rotate( 0deg);
    width: 15px;
    position: relative;
}



/* Landscape phones and down */

@media (min-width: 318px) and (max-width: 480px){
    #root > div > div > div.left__content > h1{
      font-size: 22px;
      line-height: 22px;
       margin: 0 auto;
       padding-top: 60px;
    }
    #root > div > div > div.left__content > p{
      font-size: 16px;
      text-align: center;
      line-height: 20px;
    }
    #root > div > div > div:nth-child(2) > section.grid__container > div:nth-child(n){
        padding: 12px 18px;
    }
  

  }
  
@media (min-width: 480px) and (max-width: 767px){
    #root > div > div > div.left__content > h1{
        font-family: IBM Plex Mono;
        font-style: normal;
        font-weight: bold;
        font-size: 11vw;
        line-height: 13vw;
    }

   

    .left__content{
        grid-gap: 32px;
    }


    #myLinks > a:nth-child(n){
        font-size: 14px;
        padding: 0px 10px;
    }

 
    
 }

/* Landscape phone to portrait tablet */
@media (max-width: 767px) { 
    #root > div > div > div.left__content > section:nth-child(4){
        display: none;
    }

}

/* Portrait tablet to landscape and desktop */
@media (min-width: 768px) and (max-width: 1200px) { 
    #root > div > div > div.left__content > section:nth-child(4){
        display: none;
    }
}

/* Large desktop */
@media (min-width: 1200px) { 
    #root > div > div > div.left__content > nav{
        display: none;
    }

   
}