h1, h2, h3, h4{
  margin-block-end: unset;
  margin-block-start: unset;
}

.grid__container{
    display: grid;
    grid-gap: 16px 0px ;
    overflow-y: auto;
    overflow-x: hidden;
    height: 728px;
    scrollbar-color: rgb(210,210,210) rgb(46,54,69) !important;
    scrollbar-width: thin !important;
    position: relative;
}

.grid__container > div:nth-child(n){
  background: #5E5E5E;
  text-align: center;
  text-decoration: none;
  padding: 32px 12px 0px 48px;
  text-align: left;
  height: 200px;
  opacity: 0.6;
  transition: 0.3s ease-in-out;
}

#root > div > div > div:nth-child(2) > section.grid__container > div:nth-child(n) > h3{
  color: #ffffff;
  font-family: IBM Plex Sans !important;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 24px !important;
  text-transform: uppercase;
}

#root > div > div > div:nth-child(2) > section.grid__container > div:nth-child(n) > h2{
  color: #ffffff;
  font-family: IBM Plex Mono;
  font-style: normal;
  font-weight: bold;
  font-size: 32px;
  line-height: 32px;
  padding: 14px 0px 8px 0px;
}


#root > div > div > div:nth-child(2) > section.grid__container > div:nth-child(n) > h4{
  color: #ffffff;
  font-family: IBM Plex Sans !important;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 24px;
}

#root > div > div > div:nth-child(2) > section.grid__container > div:nth-child(n) > span{
  color: #ffffff;
  font-family: IBM Plex Sans !important;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 24px !important;
}

#root > div > div > div:nth-child(2){
}

#root > div > div > div:nth-child(n) > section:nth-child(2){

  font-family: IBM Plex Sans;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 24px;
  grid-gap: 32px 0px;
  position: relative;
  top: 144px;
}


#root > div > div > div:nth-child(n) > section.grid__container > div:nth-child(n) > span{
  color:  white !important;
  float: right;
}

#root > div > div > div:nth-child(n) > section.grid__container > div:nth-child(n) > span > a > svg {
  color: grey;
  padding: 12px;
}

/* -------  HOVER ------- */

#root > div > div > div:nth-child(n) > section.grid__container > div.grid__container-grid:hover{
  opacity: 1;
}


#root > div > div > div:nth-child(n) > section.grid__container > div:nth-child(n) > span > a > svg:hover{
  color: #ffffff;
}


