.grid__container{
    display: grid;
    grid-gap: 16px 0px ;
    overflow-y: auto;
    overflow-x: hidden;
    height: 728px;
    scrollbar-color: rgb(210,210,210) rgb(46,54,69) !important;
    scrollbar-width: thin !important;
    position: relative;
}

.grid__container > div:nth-child(n){
  background: #5E5E5E;
  text-align: center;
  text-decoration: none;
  padding: 32px 12px 0px 48px;
  text-align: left;
  height: 200px;
  opacity: 0.6;
  -webkit-transition: 0.3s ease-in-out;
