#myLinks {
    display: none;
    float: left;
    right: 45px;
    position: relative;
    top: -8px;
}

#myLinks > a:nth-child(n){
    color: #B2B2B2;
    font-weight: bold;
    font-size: 14px;
    line-height: 24px;
    word-spacing: 52px;
    text-decoration: none;
    text-transform: uppercase;
    padding-right: 12px;
}

#root > div > div > div.left__content > nav > div{
    cursor: pointer;
    float: left;
    width: 20px;
