}


.top__bar{
    display: block;
    border: 1px solid #B2B2B2;
    -webkit-transform: rotate( 0deg);
            transform: rotate( 0deg);
    width: 15px;
    top: -3px;
    position: relative;
}


.bottom__bar{
    display: block;
    border: 1px solid #B2B2B2;
    -webkit-transform: rotate( 0deg);
            transform: rotate( 0deg);
    width: 15px;
    position: relative;
}



