*{
    font-family: 'IBM Plex Mono' !important;
}

.nav__content {
    display: grid;
    grid-template-columns: minmax(min-content, 576px) minmax(min-content, 656px);
    justify-content: space-between;	
    margin: 0 auto;
    
}



/* Landscape phones and down */
@media (max-width: 480px) {
    .nav__content{
        grid-template-columns: minmax(min-content, 738px);
        grid-template-rows: 400px auto;
        grid-gap: 48px 0px;
        padding: 0px !important;
    }

    #root > div > div > div.left__content > section:nth-child(4){
        margin-top: 0px;
    }

    .left__content{
        grid-gap: 32px;
    }

    #root > div > div > div.left__content > section:nth-child(5){
        display: none;
    }
 }

/* Landscape phone to portrait tablet */
@media (max-width: 767px) { 
    .nav__content{
        grid-template-columns: minmax(min-content, 738px);
        grid-gap: 48px 0px;
        padding: 48px 48px 88px 48px;
    }

    #proj_contact{
        display: none;
    }

    #root > div > div > div.left__content > section:nth-child(5){
        display: none;
    }

    #root > div > div > div.left__content > section:nth-child(4){
        display: none;
    }
    #root > div > div > div.left__content > section:nth-child(5) > section{
        display: none;
    }

    #root > div > div{
        padding-bottom: 88px;
      }
}

/* Portrait tablet to landscape and desktop */
@media (min-width: 768px) and (max-width: 979px) { 
    .nav__content{
        grid-template-columns: minmax(min-content, 738px);
        grid-gap: 48px 0px;
        margin: 0 auto;
        width: fit-content;
    }

    #proj_contact{
        display: none;
    }

    #root > div > div > div.left__content > section:nth-child(5){
        display: none;
    }

    #root > div > div > div.left__content > section:nth-child(4){
        display: none;
    }
    #root > div > div > div.left__content > section:nth-child(5) > section{
        display: none;
    }

    #root > div > div{
        padding-bottom: 88px;
      }
}

